import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
// 导入全局样式文件
import './styles/index.less'

// 导入动态设置rem基准值
import 'amfe-flexible'

// 导入所有组件
import Vant from 'vant';
import 'vant/lib/index.css';
// 处理相对时间
import './utils/dayjs'
Vue.use(Vant);
Vue.config.productionTip = false

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
